"sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100..700;1,100..700&display=swap');\n\n@font-face {\n  font-family: 'Old Stamper';\n  src: url('./assets/fonts/old_stamper.ttf');\n}\n\n@font-face {\n  font-family: 'D Day Stencil';\n  src: url('./assets/fonts/D Day Stencil.ttf');\n}\n\n@mixin flex-container($direction: row, $justify: center, $align: center, $gap: 0px) {\n  display: flex;\n  flex-direction: $direction;\n  justify-content: $justify;\n  align-items: $align;\n  gap: $gap;\n}\n\n@mixin box-shadow($color: #000000aa, $top: 0, $left: 0, $blur: 10px, $spread: 0px) {\n  box-shadow: $top $left $blur $spread $color;\n}\n\n@mixin button($width: 24px, $height: 24px, $image-size: 80%, $hover-bg: #00000033, $start-opacity: 100%) {\n  @include flex-container;\n  width: $width;\n  height: $height;\n\n  border-radius: 8px;\n  background-color: transparent;\n  border: none;\n  outline: none;\n  opacity: $start-opacity;\n  scale: 1;\n\n  transition: background-color 0.3s ease, scale 0.3s ease, opacity 0.3s ease;\n  cursor: pointer;\n\n  img {\n    height: $image-size;\n  }\n\n  &:hover {\n    background-color: $hover-bg;\n    opacity: 100%;\n  }\n\n  &:active {\n    scale: 0.9;\n  }\n}\n\n@keyframes animatedBackground {\n  from {\n    background-position: 0 0;\n  }\n  to {\n    background-position: 1000px 0;\n  }\n}\n\n@keyframes typing {\n  from { width: 0 }\n  to { width: 100% }\n}\n\n@keyframes flicker {\n  0%   { opacity:0.5; }\n  50%  { opacity:0.25; }\n  100% { opacity:0.5; }\n}\n\n\n$ship-relative-sizes: (\n  'Carrier': 5,\n  'Battleship': 4,\n  'Destroyer': 3,\n  'Submarine': 3,\n  'Patrol Boat': 2,\n);\n\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\nimg {\n  -webkit-user-drag: none;\n  user-select: none;\n  -moz-user-select: none;\n  -webkit-user-select: none;\n  -ms-user-select: none;\n\n  &[src*=svg] {\n    height: 50px;\n  }\n}\n\nbody {\n  font-family: \"Josefin Sans\", sans-serif;\n  height: 100vh;\n  width: 100vw;\n\n  background-image: url('./assets/images/background.png');\n  background-repeat: no-repeat;\n  background-position: fixed;\n  object-fit: cover;\n  overflow: hidden;\n  user-select: none;\n\n  @include flex-container;\n\n  .home-btn,\n  .sound-btn {\n    @include button(32px, 32px, 80%, #00000011, 70%);\n    display: none;\n\n    position: absolute;\n    top: 8px;\n    z-index: 3;\n\n    border: 0;\n    outline: 0;\n    filter: invert(1);\n  }\n\n  .home-btn {\n    left: 8px;\n  }\n\n  .sound-btn {\n    right: 8px;\n\n    &__icon {\n      display: none;\n    }\n\n    &__icon--active {\n      display: block;\n    }\n  }\n}\n\n.modal {\n  overflow: hidden;\n  position: absolute;\n  z-index: 2;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  user-select: none;\n\n  padding: 16px 24px;\n\n  @include box-shadow(#00000022, 0, 0, 5px, 3px);\n  background: rgb(56,134,199);\n  background: radial-gradient(circle, #276ca5dd 30%, #215b8bdd 100%);\n  border-radius: 24px;\n  border: none;\n  outline: none;\n\n  &::backdrop {\n    background: url('./assets/images/fog.png'), #000000dd;\n    background-position: 0px 0px;\n    background-repeat: repeat-x;\n    animation: animatedBackground 16s linear infinite;\n\n    opacity: 10%;\n  }\n}\n\n.home.home--active {\n  @include flex-container(column, center, center, 12px);\n}\n\n.place--active,\n.game--active {\n  display: grid !important;\n}\n\n.home {\n  display: none;\n\n  height: 500px;\n  width: 650px;\n\n  .logo {\n    @include flex-container(row, center, center, 12px);\n\n    filter: invert(1) drop-shadow(0px 0px 3px #00000077);;\n    opacity: 0.9;\n    user-select: none;\n\n    &__icon {\n      opacity: 0.85;\n\n      &--1 {\n        height: 72px;\n      }\n      \n      &--2 {\n        height: 56px;\n      }\n    }\n\n    &__title {\n      font-family: \"Old Stamper\", \"Josefin Sans\", sans-serif;\n      font-size: 48px;\n\n    }\n  }\n\n  .players {\n    @include flex-container(row, center, center, 16px);\n\n    &__vs {\n      opacity: 90%;\n      color: white;\n      font-size: 64px;\n      font-family: 'D Day Stencil', \"Josefin Sans\", sans-serif;\n      text-shadow: 0 0 3px #000000bb;\n      user-select: none;\n    }\n\n    .player {\n      @include flex-container(column, center, center, 8px);\n      @include box-shadow(#00000055, 0, 0, 7px, 0);\n\n      padding: 24px 32px;\n      border-radius: 16px;\n\n      background: radial-gradient(circle, #8eb7f577 0%, #6c91c777 150%);\n\n      &__avatar {\n        height: 168px;\n        border-radius: 50%;\n\n        @include box-shadow(#00000077, 0, 0, 2px, 1px);\n        margin-bottom: 4px;\n      }\n\n      &__name {\n        max-width: 160px;\n        border: none;\n        outline: none;\n        \n        font-size: 20px;\n        text-wrap: nowrap;\n        overflow: hidden;\n\n        padding: 4px 6px 2px 6px;\n        border-radius: 4px;\n        margin-bottom: 8px;\n\n        background-color: transparent;\n        transition: background-color 0.1s ease;\n\n        &:focus {\n          background-color: #ffffffcc;\n          @include box-shadow(#00000044, 0, 0, 2px, 1px);\n        }\n      }\n\n      &__options {\n        @include flex-container(row, center, center, 12px);\n\n        input[type=radio] {\n          display: none;\n        }\n\n        input[type=radio]:checked + label {\n          background-color: #202074;\n          opacity: 90%;\n        }\n\n        label {\n          @include button(min-content, 36px, 80%, #202074cc);\n\n          padding: 4px 8px;\n          border-radius: 8px;\n          \n          opacity: 70%;\n          border: 2px #202074cc solid;\n\n          color: white;\n          font-size: 0.9rem;\n        }\n      }\n\n      // If one option in human or AI is checked, hide the other avatar\n      &:has(.player__options input[value=true]:checked) .player__avatar--human,\n      &:has(.player__options input[value=false]:checked) .player__avatar--ai {\